import { createTheme } from '@mui/material/styles';

// Create themes for both light and dark modes
const getTheme = (mode = 'light') => {
  return createTheme({
    palette: {
      mode,
      ...(mode === 'light' 
        ? {
            // Light mode colors
            primary: {
              main: '#1976d2',
              light: '#42a5f5',
              dark: '#1565c0',
            },
            secondary: {
              main: '#2e7d32',
              light: '#4caf50',
              dark: '#1b5e20',
            },
            background: {
              default: '#f5f7fa',
              paper: '#ffffff',
            },
            text: {
              primary: '#2a3642',
              secondary: '#546e7a',
            },
          }
        : {
            // Dark mode colors
            primary: {
              main: '#42a5f5',
              light: '#80d6ff',
              dark: '#0077c2',
            },
            secondary: {
              main: '#66bb6a',
              light: '#98ee99',
              dark: '#338a3e',
            },
            background: {
              default: '#1c2531',
              paper: '#2a3642',
            },
            text: {
              primary: '#ffffff',
              secondary: '#b0bec5',
            },
          }),
    },
    typography: {
      fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
      h1: {
        fontSize: '2.5rem',
        fontWeight: 700,
      },
      h2: {
        fontSize: '2rem',
        fontWeight: 600,
      },
      h3: {
        fontSize: '1.5rem',
        fontWeight: 600,
      },
      button: {
        textTransform: 'none',
        fontWeight: 500,
      },
    },
    components: {
      MuiButton: {
        styleOverrides: {
          root: {
            borderRadius: 8,
            padding: '10px 24px',
            boxShadow: '0 2px 5px rgba(0, 0, 0, 0.08)',
          },
          contained: {
            '&:hover': {
              boxShadow: '0 4px 10px rgba(0, 0, 0, 0.12)',
            },
          },
        },
      },
      MuiCard: {
        styleOverrides: {
          root: {
            borderRadius: 12,
            boxShadow: '0 4px 20px rgba(0, 0, 0, 0.05)',
          },
        },
      },
    },
  });
};

export default getTheme;
