import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { Box, ThemeProvider, CssBaseline, Typography } from '@mui/material';

// Layout components
import Header from './components/layout/Header';
import Footer from './components/layout/Footer';

// Page components
import LandingPage from './pages/LandingPage';
import ChatPage from './pages/ChatPage';
import CalculatorPage from './pages/CalculatorPage';
import InfoHubPage from './pages/InfoHubPage';
import SettingsPage from './pages/SettingsPage';

// Context providers
import { ChatProvider } from './contexts/ChatContext';
import { SettingsProvider, useSettings } from './contexts/SettingsContext';

// Import the theme function
import getTheme from './theme';

// App wrapper that handles theme based on settings
const AppContent = () => {
  const { settings } = useSettings();
  const theme = getTheme(settings.darkMode ? 'dark' : 'light');

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Router>
        <Box sx={{ display: 'flex', flexDirection: 'column', minHeight: '100vh' }}>
          <Header />
          <Box component="main" sx={{ flexGrow: 1 }}>
            <Routes>
              <Route path="/" element={<LandingPage />} />
              <Route path="/chat" element={<ChatPage />} />
              <Route path="/calculator" element={<CalculatorPage />} />
              <Route path="/info" element={<InfoHubPage />} />
              <Route path="/settings" element={<SettingsPage />} />
              <Route path="*" element={<Navigate to="/" replace />} />
            </Routes>
          </Box>
          <Footer />
        </Box>
      </Router>
    </ThemeProvider>
  );
};

function App() {
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // Simulating app initialization
    setTimeout(() => {
      setLoading(false);
    }, 1000);
  }, []);

  if (loading) {
    return (
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          height: '100vh',
          background: '#f5f7fa'
        }}
      >
        <Typography variant="h4">Loading Tax Assistant...</Typography>
      </Box>
    );
  }

  return (
    <SettingsProvider>
      <ChatProvider>
        <AppContent />
      </ChatProvider>
    </SettingsProvider>
  );
}

export default App;
