import React, { createContext, useState, useContext, useCallback } from 'react';

const ChatContext = createContext();

export const useChat = () => useContext(ChatContext);

export const ChatProvider = ({ children }) => {
  const [messages, setMessages] = useState([
    {
      id: 'welcome',
      role: 'assistant',
      content: 'Hello! I\'m your AI Tax Assistant. How can I help you with tax-related questions today?',
      timestamp: new Date().toISOString(),
    },
  ]);
  const [isTyping, setIsTyping] = useState(false);
  const [error, setError] = useState(null);

  // Send a message to the chatbot - simplified mock for development
  const sendMessage = useCallback(async (content) => {
    if (!content.trim()) return;
    
    // Create user message
    const userMessage = {
      id: `user-${Date.now()}`,
      role: 'user',
      content,
      timestamp: new Date().toISOString(),
    };
    
    setMessages(prev => [...prev, userMessage]);
    setIsTyping(true);
    setError(null);
    
    // Mock API response for development - simulates delay
    setTimeout(() => {
      // Generate a mock response based on the user's query
      let response = "I'm sorry, I don't have specific information about that tax topic. In a production environment, this would connect to the Groq API for a proper response.";
      
      // Simple keyword-based responses for testing
      if (content.toLowerCase().includes('deduction')) {
        response = "Tax deductions reduce your taxable income. Common deductions include mortgage interest, state and local taxes, charitable contributions, and certain business expenses. The standard deduction for 2023 is $13,850 for single filers and $27,700 for married couples filing jointly.";
      } else if (content.toLowerCase().includes('credit')) {
        response = "Tax credits directly reduce the amount of tax you owe, dollar for dollar. Some common tax credits include the Child Tax Credit, Earned Income Tax Credit, and education credits like the American Opportunity Credit.";
      } else if (content.toLowerCase().includes('calculate')) {
        response = "To calculate your tax liability, start with your gross income, subtract adjustments to get your adjusted gross income (AGI), then subtract either the standard deduction or itemized deductions. The resulting amount is your taxable income, which is used to determine your tax using the progressive tax brackets.";
      }

      // Create assistant message
      const assistantMessage = {
        id: `assistant-${Date.now()}`,
        role: 'assistant',
        content: response,
        timestamp: new Date().toISOString(),
      };
      
      setMessages(prev => [...prev, assistantMessage]);
      setIsTyping(false);
    }, 1000);
  }, []);

  // Clear chat history
  const clearChat = useCallback(() => {
    setMessages([
      {
        id: 'welcome',
        role: 'assistant',
        content: 'Hello! I\'m your AI Tax Assistant. How can I help you with tax-related questions today?',
        timestamp: new Date().toISOString(),
      },
    ]);
    setError(null);
  }, []);

  return (
    <ChatContext.Provider value={{ 
      messages, 
      isTyping, 
      error, 
      sendMessage, 
      clearChat 
    }}>
      {children}
    </ChatContext.Provider>
  );
};
